#our-services{
    background: #117ae3;
    display:flex;
    flex-wrap: wrap;
    justify-content: space-between;
    font-size: 2em; /* 28 */
    height: 50vh;
    &>a{
        position: relative;
        display: inline-block;
        line-height: 1;
        text-align: right;
        height: 25vh;
        overflow: hidden;
        /*
        padding-right: 40px;
        padding-top: 15vh;
        */
        &:any-link{
            text-decoration: none;
            color: #fff;
        }

    }
    figure{
        margin:0;
        padding:0;

    }
    figcaption{
            position: absolute;
            width:100%;
            height: 100%;
            top:0;
            bottom:0;
            z-index: 202;
            padding: 40px;
        }
    video{
        position: absolute;
        top: 50%;
        left: 50%;
        min-width: 100%;
        min-height: 100%;
        width: auto;
        height: auto;
        transform: translateX(-50%) translateY(-50%);
        z-index:100;
        background-size: cover;
    }
    .development, .outsourcing{
        flex: 50%;
    }
    .consalting,
    .testing,
    .automation{
        flex: 33%;
    }
    .development{
        background: #117ae3;
        figcaption{
            background: #117ae399;
        }
        video{
            background: url(../img/develop.png) no-repeat;
        }
    }
    .outsourcing{
        background: #515459;
        figcaption{
            background: #51545999;
        }
        video{
            background: url(../img/develop.png) no-repeat;
        }
    }
    .consalting{
        background: #74797f;
        figcaption{
            background: #74797f99;
        }
        video{
            background: url(../img/develop.png) no-repeat;
        }
    }
    .testing{
        background: #4f6e8b;
        figcaption{
            background: #4f6e8b99;
        }
        video{
            background: url(../img/develop.png) no-repeat;
        }
    }
    .automation{
        background: #333f4b;
        figcaption{
            background: #333f4b99;
        }
        video{
            background: url(../img/develop.png) no-repeat;
        }
    }
    dl{
        position:absolute;
        left:0;
        width:100%;
        top: 100%;

        margin-top: -1.6em;
        transition: 0.5s;
        dt{
            padding-right:40px;
            display:block;
            height: 1.6em;
            font-family: 'Roboto Slab', serif;
        }
        dd{
            padding-right:40px;
            font-size: 0.7em;
            line-height: 1.5;
            ul{
                li{
                    display: block;
                }
            }
        }
    }
    .automation dl{
        margin-top: -2.57em;
        dt{
            height: 2.57em;
        }
    }
    &>a:hover dl{
        top: 2.2em;
    }
    &>a.automation:hover dl{
        top: 3em;
    }
}

@media only screen and (max-width: 800px) {
    #our-services{
      .consalting,
      .testing,
      .automation,
      .development,
      .outsourcing
      {
          flex: 100%;
      }
    }
}
