#what-we-do{
    background: #fff;
    position: relative;
    z-index: 99;
    color: #1c1c1c;
    text-align: center;
    min-height: 50vh;
    padding: 15vh 0 6vh 0;
    .content{
        max-width: 920px;
        margin: 0 auto;
        h2{
            margin:0;
            font-size: 4em;
            font-family: 'Roboto Slab', serif;
        }
        p{
            display: block;
            margin: 2.66em 0;
            padding:0;
            font-size: 1.33em; /* 20 */
        }
        .btn{
            display: inline-block;
            white-space: nowrap;
            font-size:  1.14em;
            line-height: 1;
            display: inline-block;
            padding: 0.67em 1.5em;
            border: 2px solid #0e77e0;
            transition: 0.1s;
            text-transform: uppercase;
            &:any-link{
                text-decoration: none;
                color: #1c1c1c;
            }
            &:hover{
                background: #0e77e0;
                color: #fff;
            }
        }
    }
}