#footer{
    width:100%;
    background: #fff;
    min-height: 250px;
    font-size: 1em;
    padding: 3.57em 0;
    .content{
        display:flex;
        justify-content:space-between;
        max-width: 1170px;
        margin: 0 auto;
        padding:2em 0;
        .social-links{
            width: 50%;
            text-align:right;
            ul,li{
                display: inline-block;
            }
            svg{
                width: 2em;
                height: 2em;
            }
            li{
                margin-left: 1em;
            }
        }
        .vcard{
            width: 50%;
            a:any-link{
                text-decoration: none;
                color: #1c1c1c;
                transition: 0.1s;
                border-bottom: 1px solid #1c1c1c77;
            }
            a:hover{
            color:  #0e77e0;
            border-bottom: 1px solid #0e77e077;
        }
        }
    }
    
        .logo{
            text-align:center;
            width:100%;
            img{
                height:3.57em;
                width: auto;
            }
            
        }
        .all-right-reserved{
            color: #636363;
            text-align:center;
            width:100%;
        }
        
}