#contact-us {
    background: #0a0a0a url(../img/stars.png) center center no-repeat;
    background-size: cover;
    width: 100%;
    min-height: 640px;
    padding: 8.9em 0 8.2em 0;
    color: #fff;
    position: relative;
    font-size: 1em;
    .info{
        max-width: 920px;
        margin: 0 auto;
        text-align:center;
        line-height: 2;
        a{
            padding: .1em .4em;
        }
        a:any-link{
            text-decoration:none;
            transition: 0.1s;
            color: #fff;
            background-color: #0e77e0ff;
        }
        a:hover{
            background-color: #0e77e000;
        }
        p{
            color: #fff;
            display: block;
            margin: 2.66em 0;
            padding:0;
            font-size: 1.33em; /* 20 */
        }
    }
    h2 {
        text-align: center;
        position: relative;
        z-index: 3;
        font-size: 4em;
        color: #fff;
        font-family: Roboto Slab, serif;
        margin-bottom: 1em;
    }
    form {
        position: relative;
        padding: 0;
        border: 0;
        width: 100%;
        font-size: 1.28em;/*18*/
        margin:0;
        text-align:center;
        z-index:100;
        .content, .btn{
          opacity: 1;
          transition: 0.5s;
        }
        &.blocked{
            .content, .btn{
              opacity: 0.4;
            }
            &:before{
              content: ' ';
              z-index:101;
              position: absolute;
              top:0;
              left:0;
              width:100%;
              height:100%;
              bottom:0;
              right:0;
            }
        }
        &.sended{
          .content, .btn{
            opacity: 0;
          }
        }
        .send-ok{
          z-index:102;
          opacity: 1;
          position: absolute;
          text-align:center;
          line-height: 2;
          top:50%;
          left:50%;
          transform: translate(-50%,-50%);
          a{
              padding: .1em .4em;
          }
          a:any-link{
              text-decoration:none;
              transition: 0.1s;
              color: #fff;
              background-color: #0e77e0ff;
          }
          a:hover{
              background-color: #0e77e000;
          }
        }
        .content {
            margin: 0 auto;
            max-width: calc(790px + 60px);
            display: flex;
            flex-flow: wrap;
            *:focus {
                outline: none;
            }
            &>div {
                position: relative;
            }
            .form-input {
                flex: 0 1 395px;
                margin: 0 15px;
            }
            .form-text {
                flex: 0 1 calc(790px + 30px);
                margin: 2em 15px;
            }
            label {
                position: absolute;
                transition: 0.2s;
                pointer-events: none;
                top: 0.6em;
                left: 0;
                font-weight: bold;
                color: #aaa;
                text-transform: uppercase;
            }
            .fi {
                width: 100%;
                border: 0;
                background: transparent;
                color: #fff;
                border-bottom: 2px solid #5f6366;
                padding: 0.4em 0;
                font-size: 1.28em;
                /*18*/
            }
            .fi:focus+label,
            .fi.has-text+label,
            .fi:not([value=""])+label {
                /*top: -20px;*/
                transform: translate3d(0, -1.8em, 0);
            }
        }
        .btn {
                display: inline-block;
                background: transparent;
                color: #fff;
                white-space: nowrap;
                font-size: 1.14em;
                line-height: 1;
                display: inline-block;
                padding: 0.67em 1.5em;
                border: 2px solid #0e77e0;
                transition: 0.1s;
                text-transform: uppercase;
                &:any-link {
                    text-decoration: none;
                    color: #1c1c1c;
                }
                &:hover {
                    background: #0e77e0;
                    color: #fff;
                }
            }
    }
}
