#who-we-are {
    position: relative;
    z-index: 99;
    height: 600px;
    height: 100vh;
    min-height: 600px;
    background: #0a0a0a url(../img/stars.png) center center no-repeat;
    background-size: cover;
    font-family: 'Roboto Slab', serif;
    color: #fff;
    h1 {
        z-index: 200;
        position: absolute;
        text-align: center;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        font-size: 6vh;
        line-height: 1;
        font-weight: normal;
        .linkorn-company {
            display: block;
            font-size: 1em;
            white-space: nowrap;
            /*font-weight: bold;*/
        }
        .makes {
            display: block;
            font-size: 0.6em;
            line-height: 1.6;
        }
        a:any-link {
            color: #fff;
            position: relative;
            /* background: #0e77e0; */
            text-decoration: none;
            padding: 3px;
            overflow: hidden;
        }
        a {
            &:before {
                position: absolute;
                content: ' ';
                background: #0e77e0;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                z-index: -1;
                transition: 0.2s;
            }
            &:hover:before {
                background: #0e77e000;
            }
        }
    }
    .bg {
        visibility: visible;
        position: relative;
        width: 100%;
        height: 100%;
        top: 5%;
        bottom: 0;
        z-index: 100;
        overflow: hidden;
        img {
            -webkit-perspective: 1000;
            -webkit-backface-visibility: hidden;
            position: absolute;
            left: 50%;
            top: 50%;
            z-index: 102;
            &:nth-child(1) {
                width: 80vmin;
                height: 80vmin;
                margin-left: -40vmin;
                margin-top: -40vmin;
                animation: spin 20s linear infinite;
                animation-delay: -1s;
            }
            &:nth-child(2) {
                width: 70vmin;
                height: 70vmin;
                margin-left: -35vmin;
                margin-top: -35vmin;
                animation: spin 16s linear infinite;
                animation-delay: -4s;
            }
            &:nth-child(3) {
                width: 60vmin;
                height: 60vmin;
                margin-left: -30vmin;
                margin-top: -30vmin;
                animation: spin 12s linear infinite;
                animation-delay: -6s;
            }
            &:nth-child(4) {
                width: 50vmin;
                height: 50vmin;
                margin-left: -25vmin;
                margin-top: -25vmin;
                animation: spin 8s linear infinite;
                animation-delay: -10s;
            }
        }
    }
    #social-links {
        visibility: visible;
        a:any-link {
            text-decoration: none;
            color: #0e77e0;
        }
        user-select: none;
        position: absolute;
        z-index: 201;
        margin:0;
        padding:0;
        list-style:none;
        right:0;
        top: 50%;
        transform: translate(0, -50%);
        height: 14.28em;
        display: flex;
        justify-content: space-between;
        flex-direction: column;
        li {
            list-style: none;
            height: 1.5em;
            svg {
                fill: #0e77e0;
                transition: 0.2s;
                height: 100%;
                width: 3em;
                padding-right: 1.48em;
            }
            a:hover svg{
                fill: #fff;
            }
        }
    }
    .to-what-we-do {
        visibility: visible;
        user-select: none;
        position: absolute;
        transform: translate(-50%);
        z-index: 200;
        left: 50%;
        padding-bottom: 1.48em;
        bottom: 0;
        width: 3.5em;
        transition: 0.2s;
        img {
            width: 100%;
            height: auto;
        }
        &:any-link {
            text-decoration: none;
            color: #fff;
        }
        &:hover {
            padding-bottom: 1em;
        }
    }
}

@keyframes spin {
    100% {
        transform: rotate(-360deg);
    }
}

@media only screen and (max-height: 600px) {
    #who-we-are {
        h1 {
            font-size: 2.6em;
        }
    }
}
