#our-clients{
    background-color: #f0f0f0;
    font-size: 1em;
    .content{
        display:flex;
        justify-content: space-between;
        max-width: 1700px;
        margin: 0 auto;
        &>div{
            width: 50%;
            position: relative;
            min-height: 20em;
        }
    }
    .clients-list{
      position: relative;
      margin:0 2em 0 6.78em;
      display:flex;
      justify-content: left;
      flex-flow: wrap;
      .cl{
        flex: 0 1 150px;
        display: flex;
        align-items: center;
        margin: 0 10px 20px 10px;
        vertical-align: middle;
      }
      img{
        display: inline-block;
        opacity: 0.7;
        width: 100%;
        height:auto;
      }
    }
    h2{
        position: absolute;
        top: 6em;
        line-height:1;
        left:0;
        transform-origin: top left;
        width: 6em;
        text-align:right;
        transform: rotate(-90deg);
        font-size: 2.5em;
        font-family: Roboto Slab,serif;
        margin-bottom: 1em;
    }
    blockquote{
        position: relative;
        margin:0 0 0 6.78em;
        padding:0;
        &:before{
            font-family: "Times New Roman", Times, serif;
            position: absolute;
            content: '\201C';
            font-size: 12em;
            font-weight: bold;
            color: #0e77e0;
            top: 0;
            left: 0;
            line-height:0;
            top: 0.38em;
            z-index:100;
        }
        header{
            padding-left: 8em;
            margin-bottom: 1em;
            h3{
                font-family: Roboto Slab,serif;
                font-size: 1.6em;
            }
            .title{
                font-style:italic;
            }
            .company{
                /*font-weight: bold;*/
            }
        }
        p{
                font-size: 1.28em; /* 18 */
            }
    }
}

@media only screen and (max-width: 800px) {
  #our-clients{
    .content{
      flex-flow: column;
        &>div{
            width: 100%;
        }
    }
  }
}
