#our-news{
    background: #f0f0f0;
    position:relative;
    width:100%;
    z-index: 1;
    font-size: 1em;
    padding-bottom:9.4em;
    &:before{
        content:' ';
        position: absolute;
        z-index: 2;
        top:0;
        left:0;
        width: 100%;
        height: 50%;
        background: #1f1f1f;
    }
    h2{
        text-align: center;
        position: relative;
        z-index: 3;
        font-size: 4em;
        color: #fff;
        font-family: Roboto Slab,serif;
        margin-bottom: 1em;
    }
    .content{
        position: relative;
        max-width: 1140px;
        margin: 0 auto;
        z-index: 3;
        display:flex;
        justify-content: space-between;
        a:any-link{
            text-decoration:none;
            color: #1c1c1c;
        }
        a{
            margin: 0 15px;
            height: 22.3em;
            overflow: hidden;
            display: inline-block;
            border: 1px solid #dbdbdb;
            flex: 0 1 350px;
            background-color: #fff;
            img{
                width: 100%;
                height: auto;
            }
            h3{
                font-size: 1.28em; /* 18 */
                font-weight: bold;
                margin:0.2em 0 0.8em 0;
                span{
                    transition: 0.1s;
                    border-bottom: 1px solid #0e77e000;
                }
            }
            article{
                padding: 1.71em 2.14em;
            }
            time{
                font-style:italic;
                line-height:0;
            }
        }
        a.with-image{
            p{
                display:none;
            }
        }
        a:hover{
            h3{
                span{
                    color:  #0e77e0;
                    border-bottom: 1px solid #0e77e077;
                }
            }
        }
    }
}
@media only screen and (max-width: 800px) {
    #our-news{
      .content{
        flex-flow: column;
      a{
          flex: 0 0 100%;
          height: auto;
          margin-bottom: 15px;
      }
    }
    }
}
