#our-directions{
    background: #1f1f1f;
    font-size: 1.28em; /* 18 */
    padding-bottom: 3em;
    .content{
        display:flex;
        justify-content: space-between;
        max-width: 1015px;
        flex-flow:wrap;
        margin: 0 auto;
        padding: 1.78em 0;
        padding-left: 50px;
    }
    a:any-link{
        color: #fff;
        text-decoration: none;
        font-size: 1em;
    }
    /*
    a:hover{
        font-size: 1.2em; 
    }
    */
    a{
        transition: 0.2s;
        display: inline-block;
        width: 25%;
        height: calc(1.78em + 1.78em);
        line-height: 2;
        padding: calc(1.78em/2) 0 calc(1.78em/2) calc(1.78em+0.66em); 
        &[data-direction="advertising"]{
            background: transparent url(../img/directions/megaphone.svg) no-repeat left;
            background-size: 1.78em;
        }
        &[data-direction="trading"]{
            background: transparent url(../img/directions/shopping-cart.svg) no-repeat left;
            background-size: 1.78em;
           
        }
        &[data-direction="culture"]{
            background: transparent url(../img/directions/theatre.svg) no-repeat left;
            background-size: 1.78em;
           
        }
        &[data-direction="aviation"]{
            background: transparent url(../img/directions/transport.svg) no-repeat left;
            background-size: 1.78em;
           
        }
        &[data-direction="production"]{
            background: transparent url(../img/directions/factory.svg) no-repeat left;
            background-size: 1.78em;
           
        }
        &[data-direction="medicine"]{
            background: transparent url(../img/directions/first-aid-kit.svg) no-repeat left;
            background-size: 1.78em;
        }
        &[data-direction="finance"]{
            background: transparent url(../img/directions/money.svg) no-repeat left;
            background-size: 1.78em;
        }
        &[data-direction="transport"]{
            background: transparent url(../img/directions/delivery-truck.svg) no-repeat left;
            background-size: 1.78em;
        }
    }
}