#header {
    background: #fff;
    color: #fff;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    z-index: 1000;
    font-size: 1em;
    height: 7.35em;
    display: flex;
    justify-content: space-between;
    text-transform: uppercase;
    align-items: center;
    padding: 0 4%;
    transition: all 0.3s ease;
    font-family: 'Roboto', sans-serif;
    a:any-link{
        text-decoration:none;
        color: #323232;
    }
    .linkorn-logo {
        display: inline-block;
        width: 13em;
        min-width: 10em;
        height: auto;
        margin-right: 1.43em; /* 20 */
    }
    nav {
        flex: 0 1 38%;
        ul,
        li {
            list-style: none;
            margin: 0;
            padding: 0;
            display: inline-block;
        }
        ul {
            display: flex;
            justify-content: space-around;
        }
        a{
            display: inline-block;
            padding: 0 0.1em;
            margin: 0 0.2em;
            white-space: nowrap;
            position: relative;

            &:before {
                content: "";
                position: absolute;
                width: 100%;
                height: 1px;
                bottom: 0;
                left: 0;
                background-color: #000;
                visibility: hidden;
                transform: scaleX(0);
                transition: all 0.1s ease-in-out 0s;
                }
            &:hover:before {
                visibility: visible;
                transform: scaleX(1);
            }
        }
    }
    .navigation-button{
      position: relative;
      height: 2.5em;
      width: 3em;
      overflow: hidden;
      border-top: 0.2em solid #000;
      border-bottom: 0.2em solid #000;
      span{
        position: absolute;
        text-indent: 100%;
        width: 100%;
        height: 100%;
        top:0;
        left: 0;
        &:before{
          position:absolute;
          content: " ";
          top: 1em;
          left:0;
          width: 100%;
          border-top: 0.2em solid #000;
        }
      }
    }
    .contact{
        font-size: 1.43em; /* 20 */
        display: flex;
        flex: 0 1 20.75%;
        justify-content: space-between;
        align-items: center;
        .tel{
            white-space: nowrap;
            display: inline-block;
            margin: 0 1em;
            transition: 0.1s;
            &:hover{
                color:  #0e77e0;
            }
        }
        .send-message{
            white-space: nowrap;
            font-size: 0.6em; /* 12 */
            line-height: 0;
            display: inline-block;
            padding: 1.25em 1.5em 1.2em 1.5em;
            border: 2px solid #0e77e0;
            transition: 0.1s;
            &:hover{
                background: #0e77e0;
                color: #fff;
            }
        }
    }
}

#m-nav{
  display: none;
  position: fixed;
  top:0;
  left:0;
  width: 100%;
  height: 100%;
  background-color: #0a0a0a;
  z-index: 9999;
  text-align: center;
  font-size: 3em;
  font-size: 6vh;
  padding-top: 2em;
  a:any-link{
    color: #fff;
    text-decoration: none;
    font-weight: normal;
  }
  a:hover{
    color: #0e77e0;
  }
  ul, li{
    display: block;
  }
  li{
    padding: 1vh 0;
    a{
      display: block;
      padding: 1vh 0;
    }
  }
  #close-m-nav{
    position: absolute;
    right: 0.05em;
    top: 0.05em;
    width: 1em;
    height: 1em;
    font-size: 1.8em;
  }
}

body.mobile-menu{
  overflow: hidden;
  #m-nav{
    display: block;
  }
}

@media only screen and (max-width: 1170px) {
    #header{
        font-size: 0.9em;
    }
}
@media only screen and (max-width: 992px) {
    #header{
        font-size: 0.8em;
    }
}

@media only screen and (max-width: 800px) {

    #header{
        nav{
          display: none;
        }
        .navigation-button{
          display: inline-block;
        }
    }
}

@media only screen and (max-width: 570px) {
    #header{
        font-size: 0.6em;
    }
}
